*/

.agrosoft-download,
.agrosoft-signin,
.agrosoft-enter,
.agrosoft-exit,
.agrosoft-bluetooth {
  font-family: 'florin';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
